body {
  margin: 0;
  padding: 0;
  background: linear-gradient(135deg, #ffafbd, #ffc3a0);
  font-family: 'Comic Sans MS', cursive, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  background: #fff0f5;
  padding: 2rem;
  border-radius: 2rem;
  box-shadow: 0 0 30px rgba(255, 105, 180, 0.3);
  text-align: center;
  width: 90%;
  max-width: 500px;
}

input, textarea {
  width: 100%;
  margin: 0.5rem 0;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
  font-size: 1rem;
  outline: none;
  background: #fff;
  box-shadow: 0 0 10px rgba(255, 182, 193, 0.2);
}

button {
  padding: 1rem 2rem;
  border: none;
  background-color: #ff69b4;
  color: white;
  border-radius: 1rem;
  font-size: 1.2rem;
  cursor: pointer;
  transition: 0.3s;
}

button:hover {
  background-color: #ff1493;
}